<template>
  <transition name="genresFade">
    <div class="genres">
      <Loader />
      <vs-row v-if="!LOADING">
        <vs-col vs-type="flex" vs-w="6" style="padding-top:1em">
          <vs-breadcrumb>
            <li>
              <a href="/" title="Home" v-colorChange>Home</a>
              <span class="vs-breadcrum--separator">/</span>
            </li>
            <li aria-current="page" class="active">{{UserSelectedGenres}}</li>
          </vs-breadcrumb>
        </vs-col>
        <vs-col vs-type="flex" vs-w="6" style="padding-top:1em;padding-right:1em">
          <Search />
        </vs-col>
        <vs-col
          type="flex"
          vs-justify="center"
          v-for="item in genres"
          vs-align="center"
          vs-w="3"
          style="padding:1em;width:20%"
          :key="item.id"
        >
          <router-link :to="{ name: 'movie', params: { id: item.id }}">
            <vs-card class="cardx">
              <div slot="media">
                <img :src="item.poster_path | setImagePath" />
              </div>
              <div>
                <span>{{item.overview}}</span>
              </div>
              <div slot="footer">
                <vs-row vs-justify="flex-end">
                  <vs-button type="gradient" color="danger" icon="favorite"></vs-button>
                  <vs-button color="primary" icon="turned_in_not"></vs-button>
                  <vs-button color="rgb(230,230,230)" color-text="rgb(50,50,50)" icon="settings"></vs-button>
                </vs-row>
              </div>
            </vs-card>
          </router-link>
        </vs-col>
        <Pagination :page="page" :totalPage="totalPage" @more="more" @prev="prev" />
      </vs-row>
    </div>
  </transition>
</template>

<script>
import { mapState, mapGetters } from "vuex";

const Loader = () => import("@/views/Genres/Loading.vue");
const Search = () => import("@/views/Search/Index.vue");
const Pagination = () => import("@/components/pagination.vue");

export default {
  head: {
    title: {
      inner: "Genres"
    }
  },
  computed: {
    ...mapState({
      genres: state => state.genres.genres,
      LOADING: state => state.genres.loadingGenre
    }),
    ...mapGetters({
      UserSelectedGenres: "genres/getUserSelectedGenres",
      page: "genres/getCurrentPage",
      totalPage: "genres/getTotalPage"
    })
  },
  mounted() {
    this.initGenres();
  },
  methods: {
    initGenres() {
      let slug = this.$route.params.genres;
      this.$store.dispatch("genres/fetchGenres", { slug });
    },
    more() {
      let slug = this.$route.params.genres;
      this.$store.dispatch("genres/fetchGenresMore", { slug });
    },
    prev() {
      let slug = this.$route.params.genres;
      this.$store.dispatch("genres/fetchGenresPrev", { slug });
    }
  },
  watch: {
    $route() {
      this.initGenres();
    }
  },
  components: { Loader, Search, Pagination }
};
</script>


<style lang="scss">
.genres {
  height: 100%;
  width: 100%;
  .vs-card--header {
    overflow: hidden;
    height: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .vs-card--content {
    height: 100px;
    overflow: hidden;
    text-align: left;
    font-weight: 300;
  }
  .cardx {
    cursor: pointer;
    &:hover {
      -webkit-box-shadow: 0 4px 25px 0 rgba(0, 0, 0, 0.3);
      box-shadow: 0 4px 25px 0 rgba(0, 0, 0, 0.3);
    }
  }
  .vs-breadcrumb--ol {
    .active {
      font-weight: bold;
    }
  }
}
</style>